<template>
  <v-container
    fluid
    grid-list-md>
    <core-progress-modal :show="loading" />
    <v-layout
      row
      wrap>
      <v-flex md12>
        <material-card
          color="primary"
          title="Gerar Remessa SPC">
          <v-card class="elevation-0">
            <v-card-text>
              <v-form>
                <v-layout
                  row
                  wrap>
                  <v-flex md12>
                    Informe abaixo o tipo da remessa e os parâmetros de filtro para os clientes que deseja enviar/retirar de restrição cadastral.
                  </v-flex>
                  <v-flex md2>
                    <v-autocomplete
                      v-validate="'required'"
                      v-model="filtros.servicoSPC"
                      :items="servicoSpcList"
                      :error-messages="errors.collect('Tipo')"
                      item-value="id"
                      item-text="descricao"
                      data-vv-name="Tipo"
                      label="Tipo"
                      @change="onChangeServicoSPC"
                    />
                  </v-flex>
                  <v-flex md3>
                    <v-autocomplete
                      v-validate="'required'"
                      v-model="filtros.redeId"
                      :items="redes"
                      :error-messages="errors.collect('Rede')"
                      item-value="id"
                      item-text="descricao"
                      data-vv-name="Rede"
                      label="Rede"
                      @change="carregaEmpresas"
                    />
                  </v-flex>
                  <v-flex md4>
                    <v-autocomplete
                      v-validate="'required'"
                      v-model="filtros.empresaId"
                      :items="empresas"
                      :error-messages="errors.collect('Origem')"
                      item-value="id"
                      item-text="descricao"
                      data-vv-name="Origem"
                      label="Origem"
                    />
                  </v-flex>
                  <v-flex
                    md3
                    layout
                    align-center
                  >
                    <v-text-field
                      v-validate="'required'"
                      :error-messages="errors.collect('Dias em atraso')"
                      v-model="filtros.diasEmAtraso"
                      label="Dias em atraso"
                      type="number"
                      min="0"
                    />
                    <span class="ml-4 mr-4">a</span>
                    <v-text-field
                      v-model="filtros.diasEmAtrasoAte"
                      label=""
                      type="number"
                      min="0"
                      maxlength="5"
                    />
                  </v-flex>
                  <v-flex
                    ml-1
                    md3
                    layout
                    align-center
                  >
                    <v-text-field
                      v-formata-moeda="filtros.valorEmAtraso"
                      v-money="money"
                      v-model="filtros.valorEmAtraso"
                      label="Valor em atraso"
                    />
                    <span class="ml-4 mr-4">a</span>
                    <v-text-field
                      v-formata-moeda="filtros.valorEmAtrasoAte"
                      v-money="money"
                      v-model="filtros.valorEmAtrasoAte"
                      label=""
                    />
                  </v-flex>
                  <v-flex
                    md3
                  >
                    <v-text-field
                      v-model="filtros.ultimoPagtoAte"
                      label="Ult. Pagto Até"
                      type="date"
                    />
                  </v-flex>
                  <v-flex
                    md6
                  >
                    <v-autocomplete
                      v-model="filtros.clientesEmAcordoId"
                      :items="clientesEmAcordo"
                      label="Clientes Acordo"
                      item-value="id"
                      item-text="descricao"
                    />
                  </v-flex>
                  <v-checkbox
                    v-model="filtros.flgApenasBaixas"
                    class="mt-1"
                    label="Gerar apenas as baixas"
                  />
                  <v-checkbox
                    v-model="filtros.flgNegJaNetativados"
                    class="mt-1"
                    label="Negativar clientes já negativados por outros órgãos (SPC, SERASA, etc)?"
                  />
                  <v-checkbox
                    v-model="filtros.flgNegContasCanceladas"
                    class="mt-1"
                    label="Negativar contas canceladas em perdas"
                  />
                  <v-checkbox
                    v-model="filtros.flgNegEmTodasRedes"
                    class="mt-1"
                    label="Negativar clientes em todas as redes mesmo se já negativado em alguma rede?"
                  />
                  <v-checkbox
                    v-model="filtros.flgNegApenasComProposta"
                    class="mt-1"
                    label="Negativar apenas clientes com proposta liberada/emitida?"
                  />
                </v-layout>
              </v-form>
            </v-card-text>
            <v-card-actions>
              <v-spacer />
              <v-btn
                :loading="loadingBtn"
                color="primary"
                @click="novaRemessa">Nova Remessa</v-btn>
            </v-card-actions>
          </v-card>
        </material-card>
      </v-flex>
      <v-flex md12>
        <v-card
          v-if="!flgNovaRemessa">
          <v-toolbar
            flat
            color="white">
            <v-toolbar-title>Útimas Remessas Geradas</v-toolbar-title>
            <v-divider
              class="mx-2"
              inset
              vertical />
          </v-toolbar>
          <v-progress-linear
            :active="loading"
            color="light-blue"
            indeterminate
            height="3"
          />
          <v-data-table
            :headers="headersRemessas"
            :items="items"
            :loading="true"
            :total-items="totalItems"
            :pagination.sync="pagination"
            class="elevation-1 arrow-pagintion"
          >
            <template
              slot="items"
              slot-scope="{ item }"
              ma-5>
              <td class="text-xs-center">
                <a
                  @click="downloadAnexo(item.anexoId)"
                >
                  {{ item.nomeArquivo }}
                </a>
              </td>
              <td class="text-xs-right">{{ item.qtdClientes }}</td>
              <td class="text-xs-center">{{ item.dataGeracao | moment("DD/MM/YYYY H:mm") }}</td>
              <td class="justify-end layout mr-2">
                <v-icon
                  color="primary"
                  class="mr-2"
                  title="Baixar Arquivo"
                  @click="downloadAnexo(item.anexoId)"
                >mdi-cloud-download-outline</v-icon>
              </td>
            </template>
          </v-data-table>
        </v-card>
        <v-card
          v-if="flgNovaRemessa">
          <v-toolbar
            flat
            color="white">
            <v-toolbar-title>Foram encontrados {{ clientes.length }} clientes(s). </v-toolbar-title>
          </v-toolbar>
          <v-progress-linear
            :active="loading"
            color="light-blue"
            indeterminate
            height="3"
          />
          <v-data-table
            :headers="headersClientes"
            :items="clientes"
            :loading="true"
            class="elevation-1 arrow-pagintion"
            hide-actions
          >
            <template
              slot="items"
              slot-scope="{ item }"
              ma-5>
              <td>{{ item.nomeCliente }}</td>
              <td class="text-xs-center">
                <a :href="`/cad-cliente/${item.clienteId}/${item.lojaId}`">
                  {{ item.cpf }}
                </a>
              </td>
              <td class="text-xs-center">
                <v-chip
                  v-if="item.operacao === 'Exclusão'"
                  color="green"
                  class="ml-0"
                  text-color="white"
                  small
                >
                  <v-avatar class="green darken-4">E</v-avatar>
                  {{ item.operacao }}
                </v-chip>

                <v-chip
                  v-else
                  color="red"
                  class="ml-0"
                  text-color="white"
                  small
                >
                  <v-avatar class="red darken-4">I</v-avatar>
                  {{ item.operacao }}
                </v-chip>
              </td>
              <td class="text-xs-center">{{ item.dataNascimento | moment("DD/MM/YYYY") }}</td>
              <td class="text-xs-center">{{ item.diasAtraso }}</td>
              <td class="text-xs-right">{{ item.valorAtraso.toLocaleString('pt-BR', { minimumFractionDigits: 2 }) }}</td>
              <td class="text-xs-center">{{ item.ultimoPagtoAte | moment("DD/MM/YYYY") }}</td>
              <td>{{ item.nomeLoja }}</td>
              <td class="justify-end layout mr-2">
                <a :href="`/cad-cliente/${item.clienteId}/${item.lojaId}`">
                  <v-icon
                    color="primary"
                    class="mr-2"
                  >mdi-magnify</v-icon>
                </a>
              </td>
            </template>
          </v-data-table>
          <v-card-actions>
            <v-btn
              style="color: black !important"
              outline
              @click="cancelarRemessa()">Cancelar</v-btn>
            <v-spacer />
            <v-btn
              :disabled="clientes.length == 0"
              :loading="loadingBtn"
              color="primary"
              @click="gerarRemesa()">Gerar</v-btn>
          </v-card-actions>
        </v-card>
      </v-flex>
    </v-layout>
  </v-container>
</template>

<script>
import { TiposBusiness, SPCBusiness, AnexoBusiness } from '../../business'
import { TODAS_ID, MONEY } from '../../constants'

export default {
  props: {
    // eslint-disable-next-line vue/require-default-prop
    redes: {
      type: Array,
      requided: true
    },
    // eslint-disable-next-line vue/require-default-prop
    clientesEmAcordo: {
      type: Array,
      requided: true
    },
    // eslint-disable-next-line vue/require-default-prop
    servicoSpcList: {
      type: Array,
      requided: true
    }
  },
  data () {
    return {
      money: MONEY,
      flgNovaRemessa: false,
      item: null,
      clientes: [],
      filtros: {
        redeId: TODAS_ID,
        empresaId: TODAS_ID,
        servicoSPC: null,
        clientesEmAcordoId: TODAS_ID,
        diasEmAtraso: null,
        diasEmAtrasoAte: null,
        valorEmAtraso: null,
        valorEmAtrasoAte: null,
        ultimoPagtoAte: null,
        flgApenasBaixas: false,
        flgNegJaNetativados: false,
        flgNegContasCanceladas: false,
        flgNegEmTodasRedes: false,
        flgNegApenasComProposta: false
      },
      empresas: [],
      headersRemessas: [
        {
          sortable: false,
          align: 'center',
          text: 'Arquivo',
          value: 'arquivo'
        },
        {
          sortable: false,
          align: 'right',
          text: 'Qtd. Clientes',
          value: 'qtdClientes'
        },
        {
          sortable: false,
          align: 'center',
          text: 'Data de Geração',
          value: 'dataCadastro'
        },
        {
          sortable: false,
          align: 'center'
        }
      ],
      headersClientes: [
        {
          sortable: false,
          text: 'Cliente',
          value: 'nomeCliente'
        },
        {
          sortable: false,
          text: 'CPF',
          value: 'cpf',
          align: 'center'
        },
        {
          sortable: false,
          text: 'Operacao',
          value: 'operacao',
          align: 'center'
        },
        {
          sortable: false,
          text: 'Nascimento',
          value: 'dataNascimento',
          align: 'center'
        },
        {
          sortable: false,
          text: 'Dias Atraso',
          value: 'diasAtraso',
          align: 'center'
        },
        {
          sortable: false,
          text: 'Valor em Atraso',
          value: 'valorAtraso',
          align: 'right'
        },
        {
          sortable: false,
          text: 'Último Pagamento',
          value: 'ultimoPagamento',
          align: 'center'
        },
        {
          sortable: false,
          text: 'Loja',
          value: 'nomeLoja'
        },
        {
          sortable: false,
          align: 'center'
        }
      ],
      items: [],
      pagination: {
        rowsPerPage: 15
      },
      totalItems: null,
      loading: false,
      loadingBtn: false
    }
  },
  watch: {
    pagination: {
      handler () {
        this.paginar()
      }
    }
  },
  beforeMount () {
    this.empresas.push({ id: TODAS_ID, descricao: 'Todas Origens' })
  },
  methods: {
    novaRemessa () {
      if (!this.filtros.servicoSPC) {
        this.$root.showAlerta('O campo Tipo é obrigatório')
        return
      }
      if (!this.filtros.diasEmAtraso) {
        this.$root.showAlerta('O campo Dias em atraso é obrigatório')
        return
      }
      this.flgNovaRemessa = true
      this.loading = true
      SPCBusiness.novaRemesa(this.filtros)
        .then(response => {
          this.clientes = response.data
        })
        .catch(erro => {
          this.$root.showErro(erro)
        })
        .finally(() => {
          this.loading = false
        })
    },
    gerarRemesa () {
      this.loading = true
      SPCBusiness.gerarRemesa(this.clientes, this.filtros.servicoSPC)
        .then(response => {
          this.clientes = response.data
          this.$root.showSucesso('Remessa gerada com sucesso')
          this.cancelarRemessa()
          this.paginar()
        })
        .catch(erro => {
          this.$root.showErro(erro)
          this.loading = false
        })
    },
    cancelarRemessa () {
      this.flgNovaRemessa = false
      this.clientes = []
    },
    onChangeServicoSPC (servicoSPC) {
      this.flgNovaRemessa = false
      this.filtros.servicoSPC = servicoSPC
      this.paginar()
    },
    pesquisar () {
      this.loadingBtn = true
      this.paginar()
    },
    paginar () {
      if (!this.filtros.servicoSPC) {
        return
      }
      this.loading = true
      SPCBusiness.pagination(this.pagination.rowsPerPage, this.pagination.page - 1, this.pagination.sortBy || 'id', this.filtros)
        .then(response => {
          this.items = response.data.content
          this.totalItems = response.data.totalElements
        }).catch(erro => {
          this.$root.showErro(erro)
        }).finally(() => {
          this.loading = false
        })
    },
    carregaEmpresas (redeId) {
      this.filtros.redeId = redeId
      this.filtros.empresaId = TODAS_ID
      this.empresas = []
      this.empresas.push({ id: TODAS_ID, descricao: 'Todas Origens' })
      TiposBusiness.getAllEmpresasOfRede(redeId).then(response => {
        this.empresas = this.empresas.concat(response.data)
      })
    },
    downloadAnexo (anexoId) {
      AnexoBusiness.downloadAnexo(anexoId)
    }
  }
}
</script>
